<style>
  .address .container{
    width: 95%;
    margin: 0 auto;
  }
  .address .container li{
    border-bottom: 1px solid rgb(179, 177, 177);
    margin: 20px 0px;
  }
  .address .container li>p{
    text-align: left;
  }
  .address .container>p{
    background: #ffa500;
  }
</style>
<template>
  <div class="address">
    <div class="container">
      <p>默认使用第一个收货地址</p>
      <ul>
        <li v-for="(item,i) of address" :key="i">
          <p>收货人：{{item.name}} | 邮编：{{item.postcode}} | 省市区：{{item.district}} <br/> 详细地址：{{item.detail}}</p>
        </li>
      </ul>
      <mt-button @click="add" type="primary" size="large">添加收货地址</mt-button>
    </div>
  </div>
</template>

<script>
import {Toast} from "mint-ui"
export default {
  data(){
    return {
      address: []
    }
  },
  methods:{
    add(){
      this.$router.push("/add-address")
    }
  },
  created(){
    try {
      this.address = JSON.parse(localStorage.address)
    } catch (error) {
      Toast("您还没有添加收货地址")
    }
  },
  beforeMount(){
    console.log(this.address)
  }
}
</script>